.staff {
    padding-top: $offset-section-l;
    &-list {
        display: grid;
        grid-template-columns: repeat(2, 1fr);
        column-gap: 30px;
        row-gap: 24px;
        @include mobile {
            grid-template-columns: repeat(1, 1fr);
            row-gap: 14px;
        }
    }
}